.App {
  text-align: left;
  background-color: burlywood;
	position: absolute;
  left: 0;
  top: 0;
  height: 100%;
	width: 100%;
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.New-Record-Name{
	width: calc(100% - 22px);
	margin-bottom: 5px;
}

.New-Record-Text{
	width: calc(100% - 20px);
	height: 100px;
}

.Records, .New-Record{
	margin: 10px;
}

.Record{
	background-color: beige;
	margin: 1px;
	padding: 3px;
}

.RecordName{
	margin: 10px;
	font-weight: bold;
}

.RecordValue{
	margin: 10px;	
}

.ToolsBar{
	margin: 0px 10px 0px 10px;	
	padding: 5px;
	/* background-color: cornflowerblue; */
	
}